import React from 'react'
import CardsAdd from './CardsAdd'
import { Button } from 'react-bootstrap'
function AddVegetables() {
  let card1={
    title:"potato",
    image:"https://cdn.pixabay.com/photo/2014/08/06/20/32/potatoes-411975_1280.jpg"
  }
  let card2={
    title:"tomato",
    image:"https://images-prod.healthline.com/hlcmsresource/images/AN_images/tomatoes-1296x728-feature.jpg"
  }
  let card3={
    title:"brinjal",
    image:"https://urjaseeds.com/cdn/shop/products/brinjal_ppl_900x.jpg?v=1654512410"
  }
  return (
    <div className='container c'>
      <div className="row row-cols-sm-1 row-cols-md-2 row-cols-lg-3 row-cols-5">
  <CardsAdd c={card1}/>
  <CardsAdd c={card2}/>
  <CardsAdd c={card3}/>
 
    </div>
    <Button className='btn btn-warning m-auto g-5'>Add Item</Button>
    </div>
  )
}

export default AddVegetables