import React from 'react'
import CardsAdd from './CardsAdd'
import { Button } from 'react-bootstrap'
function AddFruits() {
  let card1={
    title:"Apple",
    image:"https://images.unsplash.com/photo-1560806887-1e4cd0b6cbd6?q=80&w=1000&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxleHBsb3JlLWZlZWR8Nnx8fGVufDB8fHx8fA%3D%3D"
      }
      let card2={
        title:"Oranges",
        image:"https://cdn.britannica.com/24/174524-050-A851D3F2/Oranges.jpg"
          }
          let card3={
            title:"Grapes",
            image:"https://fastandfresh.in/cdn/shop/products/green-grapes_800x.jpg?v=1671839228"
              }
  return (
    <div>
       <div className="c container">
            <div className="row row-cols-sm-1 row-cols-md-2 row-cols-lg-3 row-cols-1">
      <CardsAdd c={card1}/>
      <CardsAdd c={card2}/>
      <CardsAdd c={card3}/>
      {/* <CardsAdd c={card4}/>
      <CardsAdd c={card5}/> */}
    </div>
    <Button className='btn btn-warning m-auto g-5'>Add Item</Button>
    </div>
    </div>
  )
}

export default AddFruits