import React from 'react'
import { Button } from 'react-bootstrap'
import { useState } from 'react'
import './CardsUsage.css'
function CardsUsage(props) {
    let [q,setQ]=useState(0)
    let decrease=()=>{
        setQ(q-1)
    }
    let increase=()=>{
        setQ(q+1)
    }
  return (
    <div>
       
            <div className='card h-100'>
                <div className='card-header text-center display-6'>
                    {props.c.title}
                </div>
                <div className='card-body'>
                    <img className='z' src={props.c.image} alt="" />  
                </div>
                <div className='row m-auto'>
                    <div className='col-4'><Button className='btn-secondary' onClick={decrease}  >-</Button></div>
                    <div className='col-4'>{q}</div>
                    <div className='col-4'><Button className='btn-secondary' onClick={increase}>+</Button></div>
                </div>
                <Button className='mt-2'>Add to Cart</Button>
            </div>
        </div>

  )
}

export default CardsUsage