import {  createContext,useState } from "react";
import axios from "axios";

// create context
export let loginContext=createContext();

// create context provider


function LoginContextProvider ({children}){
// state
let [loginStatus,setLoginStatus]=useState('')
let [loginStatusBool,setLoginStatusBool]=useState(false)

const logoutFunction=()=>{
  localStorage.clear();
   setLoginStatus('');
  setLoginStatusBool(false);
 
  }

const loginFunction=(userCredentialsObj)=>{
  axios.post("http://localhost:3500/user-api/login-user", userCredentialsObj)
    .then((response)=>{
      if(response.data.message==='logged in')
      {
        // save token to local storage
        localStorage.setItem("token",response.data.token)
         console.log('logged in')
         console.log(response.data.user)
        //  console.log(response.data.user.userType)
        //  console.log(loginStatus)
        setLoginStatus(response.data.user.userType)
        setLoginStatusBool(true)
        //  wont reflect value here --> console.log(loginStatus)
         
      }
      else{
        console.log(response.data.message)
    } 
}
     )
     .catch((err)=>{
        console.log("err in user login: ",err)
     })
}




  return (
    <loginContext.Provider value={[loginStatus,loginStatusBool,setLoginStatus,loginFunction,logoutFunction]}>
            {children}
    </loginContext.Provider>
  )
}

export default LoginContextProvider